}

.hom-infinity-trans {
background-image: linear-gradient(180deg, #0A0A0A91 100%, #F2295B00 100%), url('./about_us_page.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  padding: 10vh 0;
  height: 60vh;
  position: relative;
}

.hom-fixed-grid-container {
  font-size: 1.5vw;
  margin-left: 0vh;
