}

.homecom2 {
  height: 79vh;
  width: 100vw;
  margin-top: -20vh;
  z-index: 100;
  display: block;
  position: relative;
  overflow: hidden;
}

.home-container {
  width: 100vw;
  height: fit-content;
  overflow: hidden;
}

.infinity-trans12 {
  background-image: linear-gradient(180deg, #0A0A0A91 100%, #F2295B00 100%), url('./scrollhome.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  padding: 10vh 0;
  height: 70vh;
  width: 100vw;
  position: relative;
  backdrop-filter: blur(20px);
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  opacity: 0.94;
  transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
}

.hom-Typerwritter {
  color: #e9ecef;
  margin-left: 32vw;
  font-size: 3vw;
  margin-top: 7vh;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  width: -moz-fit-content;
  width: fit-content;
  animation: typing 3s steps(40, end), blink-caret 0.75s step-end infinite;
}
.home-img {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  color: transparent;
  -webkit-text-stroke: 1px #ffffff;
  text-stroke: 1px #ffffff;
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  opacity: 0.94;
  transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
  background-image: linear-gradient(180deg, #0A0A0A91 100%, #F2295B00 100%);
}

.app12 {
  margin-top: -13vh;
  width: 100vw;
  height: 110vh;
  display: block;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.slider1 {
  width: 100vw;
  height: 100vh;
}

.slider1 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  animation: zoomInOut 15s infinite alternate;
}

@keyframes zoomInOut {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1.2);
  }
}

.home-logoTxt {
  margin-bottom: 0.5rem;
  margin-left: 1rem;
}

.home-menu-link {
  grid-column: 3 / 4;
}

.home-menu-link ul {
  height: 10rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.home-menu-link ul li {
  font-size: 1.8rem;
}

.home-menu-link ul li a {
  text-transform: capitalize;
  color: rgba(255, 255, 255, 0.8);
  transition: 0.5s;
  font-weight: 600;
}

.home-menu-link ul li:hover > a {
  font-weight: bold;
  background-color: #22bad8;
  border-radius: 10px;
  color: rgb(250, 250, 250);
  border: none;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  animation: fillBackground 0.3s forwards;
}

@keyframes fillBackground {
  0% {
    background-color: transparent;
    height: 0;
  }
  100% {
    background-color: #073842;
    height: 100%;
  }
}

