@media (max-width: 998px) {
  .home-main-nav {
    height: 7rem;
    grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
  }

  .home-menu-link {
    display: none;
  }

  .home-logo,
  .home-social-media ul {
    height: 7rem;
  }

  .home-mobile-menu-link {
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }

  .home-mobile-menu-link {
    background-color: white;
    height: 20rem;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    padding-left: 3rem;
    transition: all 2s linear;
    transform-origin: top;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .home-mobile-menu-link ul {
    height: 20rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: start;
  }

  .home-mobile-menu-link ul li:first-child {
    transition-delay: 0.2s;
  }

  .home-social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  }

  .home-social-media .home-social-media-desktop {
    height: 0;
    display: none;
  }

  .home-social-media {
    height: 7rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .home-social-media .home-hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }

  .home-logo-image {
    width: 3rem;
    height: auto;
    margin-right: 1rem;
    margin-left: 5rem;
  }
}

